<template>
    <div class="main">
        <Breadcrumb class="breadcrumb" separator=">">
            <BreadcrumbItem to="/">{{$t('menu.home')}}</BreadcrumbItem>
            <BreadcrumbItem to="/hash">{{$t('account.tit')}}</BreadcrumbItem>            
        </Breadcrumb>
        <div class="accountinfo">
            <div class="address">{{$t('account.tit')}}{{addr}}
                <Tooltip :content="$t('qr')" placement="top"><i class="iconfont icon-erweima" @click="qr = true"></i></Tooltip>
                <Tooltip :content="$t('copy')" placement="top"><i class="iconfont icon-fuzhi" 
                v-clipboard:copy="addr"
                v-clipboard:success="copysuc"
                v-clipboard:error="copyerr"
                ></i></Tooltip>
            </div> 
            <div>
                <div class="linfo">CADT{{$t('account.account')}}</div>
                <div class="rinfo">
                    {{$t('account.account')}}{{$t('account.balance')}}
                    <Select v-model="choose" style="width:200px" class="select">
                        <Option v-for="item in selist" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                </div>
            </div>
        </div>
        <div class="accountlist">
            <Table :columns="thead" :data="data" size="small" :no-data-text="$t('nodata')" :loading="loading"></Table>            
        </div>
        <div class="more">
            <Button type="primary" ghost>{{$t('more')}}</Button>
        </div>
        <Modal
            :title="$t('account.tit')"
            footer-hide
            v-model="qr"
            class-name="vertical-center-modal">
            <div style="text-align:center">
                <vue-qr :text="addr" :logoSrc="logoSrc" colorDark="#e4526d" :size="300"></vue-qr>
            </div>
        </Modal>
    </div>
</template>

<script>
import {Table,Button,Breadcrumb,BreadcrumbItem,Modal,Tooltip,Select,Option} from 'iview'
import VueQr from 'vue-qr'
export default {
    name:'Account',
    components:{
        Table,Button,Breadcrumb,BreadcrumbItem,Modal,VueQr,Tooltip,Select,Option
    },
    data(){
        return {
            loading:false,
            qr:false,
            addr:this.$route.query.address,
            logoSrc:require('../assets/images/logo.png'),
            choose:'',
            selist:[{
                value: 'New York',
                label: 'New York'
            },
            {
                value: 'London',
                label: 'London'
            },
            {
                value: 'Sydney',
                label: 'Sydney'
            },
            {
                value: 'Ottawa',
                label: 'Ottawa'
            },
            {
                value: 'Paris',
                label: 'Paris'
            },
            {
                value: 'Canberra',
                label: 'Canberra'
            }],
            thead: [{
                title: this.$t('home.head2')[0],
                key: 'name',
                render:(h,param) => {
                return h('i',{
                    class: param.row.name ? 'iconfont icon-duihao suc' : 'iconfont icon-cuowu err',
                    style:'font-size:16px;'
                    }
                )              
                }
            },
            {
                title: this.$t('home.head2')[1],
                key: 'age'
            },
            {
                title: this.$t('home.head2')[3],
                key: 'address',
                render:(h,param) => {
                return h('div',{
                    style:'color:#e4526d'
                },
                param.row.address
                )              
                }
                                
            },
            {
                title: this.$t('home.head2')[4],
                key: 'address'
            },
            {
                title: this.$t('home.head2')[5],
                key: 'address'
            },
            {
                title: this.$t('home.head2')[6],
                key: 'address'
            }],
            data:[{name:'sdfsdf',address:'dfsefefsef'},{name:'',address:'dfsefefsef'}],
        }
    },
    created(){
        console.log(this.$route.query.address)
    },
    methods:{
        copysuc(e){
            this.$Message.success(this.$t('copysuc'))
        },
        copyerr(e){
            this.$Message.error(this.$t('copyerr'))
        }
    },

}
</script>

<style lang="less" scoped>
@import '../assets/css/var.less';

.accountinfo{
    .box();
    padding:10px 20px;
    margin-bottom:20px;
    .iconfont{
        font-size:20px;
        cursor: pointer;
        color:@txtColor2;
        padding:5px;
        &:hover{
            background:@mainbg;
            border-radius:50%;
        }
    }
    .address{
        border-bottom: 1px solid #DDDCDC;
        margin-bottom:8px;
        padding-bottom:8px;
    }
    .rinfo, .linfo{
        list-style:none;
        display: inline-block;
        width:50%;        
    }
    .rinfo{
        text-align:right;
        .select{text-align: left;margin-left:20px;}     
    }

}
.accountlist{
    .box();
    min-height: 600px;    
}
.more{
    text-align: center;
    padding:30px 0;
}
</style>
